.q-subscriptions__options-accordion {
	background: #FFFFFF;
	border: 1px solid #FFFFFF;
	box-shadow: 0 12px 23px 0 rgba(170,177,188,0.20);
	border-radius: 2px;
}
.q-subscriptions__options-handle {
	cursor: pointer;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 15px;
}
@media (min-width: 720px)  {
	.q-subscriptions__options-handle {
		padding: 30px 30px 30px 40px;
	}
}
.q-subscriptions__options-items {
	transition: all 0.5s ease;
	display: none;
	box-sizing: border-box;
}
.q-subscriptions__options-handle .plus:before,
.q-subscriptions__options-handle .plus:after {
	content: "";
	position: absolute;
	background: #30405B;
}
.q-subscriptions__options-handle .plus:before {
	transition: transform 0.5s ease-in;
	width: 20px;
	height: 2px;
	left: 0;
	top: 10px;
}
.q-subscriptions__options-handle .plus:after {
	transition: transform 0.5s ease-in;
	transform: rotate(90deg);
	opacity: 1;
	left: 0;
	top: 10px;
	width: 20px;
	height: 2px;
}
.q-subscriptions__options-handle.active .plus:before {
	transform: rotate(135deg);
}
.q-subscriptions__options-handle.active .plus:after {
	transform: rotate(225deg);
}
.q-subscriptions__options-title {
	margin: 0;
	padding-right: 15px;
}
@media (min-width: 720px)  {
	.q-subscriptions__options-title {
		padding-right: 0;
	}
}
