}
.options-toggle .options-content-heading .heading--4 {
	cursor: pointer;
	user-select: none;
}
.accordion-content .highlighted {
	color: #ED2E26;
	font-weight: 400;
}
.options-content-heading {
	position: relative;
}
.options-toggle .options-content-heading .plus {
	display: block;
	width: 14px;
	height: 14px;
	position: absolute;
	left: -28px;
	top: 10px;
}
.options-toggle .options-content-heading .plus:before,
.options-toggle .options-content-heading .plus:after {
	content: "";
	position: absolute;
	background: #6A778B;
	opacity: 1;
	transition: transform 0.35s, opacity 0.35s;
}
.options-toggle .options-content-heading .plus:before {
	width: 14px;
	height: 2px;
	top: 6px;
}
.options-toggle .options-content-heading .plus:after {
	width: 2px;
	height: 14px;
	left: 6px;
}
.options-toggle .options-content-heading.active .plus:after {
	transform: rotate(90deg);
}
.options-toggle .options-content-heading.active .plus:before {
	transform: rotate(90deg);
	opacity: 0;
}
